import api from "./../api.js";

const user = {
  state: {
    user: null,
    contacts: null
  },
  mutations: {
    setUser(state, user) {
      state.user = user;
    },
    setUserContacts(state, contacts) {
      state.contacts = contacts;
    }
  },
  actions: {
    getUserContacts(context) {
      return new Promise((resolve, reject) => {
        api
          .getAll(`/contacts`)
          .then(result => {
            context.commit("setUserContacts", result);
            resolve();
          })
          .catch(err => {
            console.log(err);
            reject();
          });
      });
    }
  },
  getters: {}
};

export default user;



