//import api from "./../api.js";

const ui = {
  state: {
    showWorkflows: true,
    activeWorkflow: null,
    draggedUser: null
  },
  mutations: {
    toggleWorkflows(state) {
      state.showWorkflows = !state.showWorkflows;
    },
    setDraggedUser(state, data) {
      state.draggedUser = data;
    }
  },
  actions: {},
  getters: {}
};

export default ui;



