<template>
    <div>
        <div class="activityContent--section" v-if="comments.length > 0">
            <h5>Comments</h5>
            <Comment 
            v-for="comment in comments" 
            v-bind:key="comment.id"
            v-bind:comment="comment"/>
        </div>
        <ExpandingCreateControl
          v-bind:toggleButtonText="'Add Comment'"
          v-bind:inputPlaceholder="'Comment Content'"
          v-bind:createButtonText="'Add'"
          v-on:createTriggered="createComment"
          />
    </div>
</template>

<script>
import ExpandingCreateControl from "./controls/ExpandingCreateControl.vue";
import Comment from "./Comment.vue";

export default {
  name: "Comments",
  props: ["comments"],
  components: {
    Comment,
    ExpandingCreateControl
  },
  mounted() {
    this.$root.$on("startCreateComment", this.startCreateComment);
  },
  beforeDestroy() {
    this.$root.$off("startCreateComment", this.startCreateComment);
  },
  methods: {
    createComment(comment, callback) {
      const update = {
        content: comment
      };

      this.$emit("commentCreated", update, function(callbackval) {
        if (callbackval) callback(true);
        else callback(callbackval);
      });
    }
  }
};
</script>

<style lang="scss" scoped>

</style>




