import api from "./../api.js";
import _ from "lodash";

const activity = {
  state: {
    activeActivity: null
  },
  mutations: {
    setActiveActivity(state, activity) {
      state.activeActivity = activity;
    },
    closeActivityDetails(state) {
      state.activeActivity = null;
    },
    updateActivityMeta(state, update) {
      //TODO - ADD TO WORKFLOW TO KEEP IT'S COPY OF ACTIVITY UPDATED
      //   const activity = state.workflow.activities.filter(
      //     o => o.id === state.activeActivity.id
      //   )[0];
      activity.description = //state.activeActivity.description =
        update.description;
      activity.name = //state.activeActivity.name =
        update.name;
    },
    addActivityData(state, update) {
      state.activeActivity[update.path].unshift(update.data);
    },
    deleteActivityData(state, item) {
      state.activeActivity[item.path] = state.activeActivity[item.path].filter(
        o => o.id != item.id
      );
    }
  },
  actions: {
    setActiveActivity(context, id) {
      api
        .getAll(
          `workflows/${
            context.rootState.workflow.metadata.id
          }/activities/${id}/details`
        )
        .then(result => {
          context.commit("setActiveActivity", result);
        })
        .catch(err => console.log(err));
    },
    updateActivityMeta(context, data) {
      api
        .updateOne(
          `workflows/${context.rootState.workflow.metadata.id}/activities/${
            context.state.activeActivity.id
          }`,
          data
        )
        .then(result => {
          context.commit("updateActivityMeta", result);
        })
        .catch(err => console.log(err));
    },
    addActivityData(context, data) {
      api
        .postOne(
          `workflows/${context.rootState.workflow.metadata.id}/activities/${
            context.state.activeActivity.id
          }/${data.path}`,
          data.data
        )
        .then(result => {
          context.commit("addActivityData", { data: result, path: data.path });
        })
        .catch(err => console.log(err));
    },
    deleteActivityData(context, data) {
      api
        .deleteOne(
          `workflows/${context.rootState.workflow.metadata.id}/activities/${
            context.state.activeActivity.id
          }/${data.path}/${data.id}`
        )
        .then(() => {
          context.commit("deleteActivityData", {
            id: data.id,
            path: data.path
          });
        })
        .catch(err => console.log(err));
    },
    assignActivity(context, data) {
      const update = {
        assignedAction: "EXECUTE",
        personId: data.userID,
        personType: data.userType
      };

      api
        .postOne(
          `workflows/${context.rootState.workflow.metadata.id}/activities/${
            data.activityID
          }/assignments`,
          update
        )
        .then(result => {
          context.commit("assignActivity", {
            id: data.activityID,
            data: result
          });
        })
        .catch(err => console.log(err));
    }
  },
  getters: {
    assignedActivityUser(state, getters, rootState) {
      if (!rootState.workflow.activities || !state.activeActivity) return null;
      const users = _.filter(
        state.activeActivity.assignments,
        o => o.assignedAction === "EXECUTE"
      ).map(x => x.assignedTo);

      return users;
    },
    subscribedActivityUser(state, getters, rootState) {
      if (!rootState.workflow.activities || !state.activeActivity) return null;
      const users = _.filter(
        state.activeActivity.assignments,
        o => o.assignedAction === "SUBSCRIBE"
      ).map(x => x.assignedTo);

      return users;
    }
  }
};

export default activity;



