import api from "./../api.js";
import _ from "lodash";

const workflow = {
  state: {
    metadata: null,
    activities: null,
    activitiesList: [],
    comments: null,
    people: null,
    attachments: null,
    hyperlinks: null
  },
  mutations: {
    resetState(state) {
      state.metadata = null;
      state.activities = null;
      state.activitiesList = [];
      state.comments = null;
      state.people = null;
      state.attachments = null;
      state.hyperlinks = null;
    },
    setActiveWorkflow(state, workflow) {
      //Object.assign(state, baseWorkflowData);
      state.metadata = workflow;
    },
    toggleWorkflows(state) {
      state.showWorkflows = !state.showWorkflows;
    },
    setActivities(state, activities) {
      const data = _.sortBy(activities, o => o.diagramNode.position.positionY);

      let list = {};

      data.forEach(activity => {
        list[activity.id] = activity;
        if (state.activitiesList.indexOf(activity.id) === -1)
          state.activitiesList.push(activity.id);
      });

      state.activities = list;
    },
    addWorkflowActivity(state, activity) {
      state.activities[activity.id] = activity;
      state.activitiesList.push(activity.id);
    },
    updateWorkflowActivities(state, update) {
      state.activities = update;
    },
    updateWorkflowOrder(state, update) {
      state.activitiesList = update.map(o => o.id);
    },
    updateWorkflowData(state, update) {
      if (update.path != "people") {
        let sorted = _.sortBy(
          update.data,
          o => o.metadata.dateMetadata.modifiedOn
        );
        state[update.path] = sorted;
        return;
      }
      state[update.path] = update.data;
    },
    addWorkflowData(state, update) {
      state[update.path].push(update.data);
    },
    deleteWorkflowData(state, item) {
      state[item.path] = state[item.path].filter(o => o.id != item.id);
    },
    addWorkflowContact(state, contact) {
      state.people.push(contact);
    },
    addPlaceholder(state, placeholder) {
      state.people.push(placeholder);
    },
    assignActivity(state, update) {
      state.activities[update.id].assignments.unshift(update.data);
    }
  },
  actions: {
    getWorkflowActivities(context) {
      const activities = new Promise((resolve, reject) => {
        api
          .getAll(
            `workflows/${
              context.rootState.workflow.metadata.id
            }/activities/diagram`
          )
          .then(result => {
            resolve(result);
          })
          .catch(err => reject(err));
      });

      const people = context.dispatch("getWorkflowData", "people");

      Promise.all([activities, people])
        .then(results => {
          context.commit("setActivities", results[0]);
        })
        .catch(err => console.log(err));
    },
    addWorkflowActivity(context, newActivity) {
      api
        .postOne(
          `workflows/${context.state.metadata.id}/activities/`,
          newActivity
        )
        .then(result => {
          context.commit("addWorkflowActivity", result);
        })
        .catch(err => console.log(err));
    },
    updateActivityPosition(context, newPosition) {
      api
        .postOne(
          `workflows/${context.state.metadata.id}/activities/${
            newPosition.activityId
          }/position`,
          newPosition
        )
        .then(result => {
          console.log("success", result);
        })
        .catch(err => console.log(err));
    },
    deleteActivity(context, id) {
      api
        .deleteOne(`workflows/${context.state.metadata.id}/activities/${id}/`)
        .then(result => {
          console.log(result);
          // context.commit('deleteActivity',result);
        })
        .catch(err => console.log(err));
    },
    getWorkflowData(context, path) {
      return new Promise((resolve, reject) => {
        api
          .getAll(`workflows/${context.state.metadata.id}/${path}`)
          .then(result => {
            context.commit("updateWorkflowData", { data: result, path: path });
            resolve();
          })
          .catch(err => {
            console.log(err);
            reject();
          });
      });
    },
    addWorkflowData(context, data) {
      return new Promise((resolve, reject) => {
        api
          .postOne(
            `workflows/${context.state.metadata.id}/${data.path}`,
            data.data
          )
          .then(result => {
            context.commit("addWorkflowData", {
              data: result,
              path: data.path
            });
            resolve(true);
          })
          .catch(err => {
            console.log(err);
            reject(err);
          });
      });
    },
    deleteWorkflowData(context, data) {
      api
        .deleteOne(
          `workflows/${context.state.metadata.id}/${data.path}/${data.id}`
        )
        .then(() => {
          context.commit("deleteWorkflowData", {
            id: data.id,
            path: data.path
          });
        })
        .catch(err => console.log(err));
    },
    uploadAttachments(context, data) {
      return new Promise((resolve, reject) => {
        api
          .postOne(data.path, data.data)
          .then(result => {
            result.forEach(attachment => {
              context.commit("addWorkflowData", {
                data: attachment,
                path: "attachments"
              });
            });
            resolve(true);
          })
          .catch(err => {
            console.log(err);
            reject(err);
          });
      });
    },
    inviteWorkflowContact(context, data) {
      api
        .postOne(`workflows/${context.state.metadata.id}/users`, data)
        .then(result => {
          context.commit("addWorkflowContact", result);
        })
        .catch(err => console.log(err));
    },
    createPlaceholder(context, name) {
      const update = {
        name: name
      };

      api
        .postOne(`/workflows/${context.state.metadata.id}/placeholders`, update)
        .then(result => {
          context.commit("addPlaceholder", result);
        })
        .catch(err => console.log(err));
    }
  },
  getters: {
    activities(state) {
      if (!state.activitiesList) return null;
      return state.activitiesList.map(
        activityId => state.activities[activityId]
      );
    },
    nextWorkflowActivity(state) {
      if (!state.activities || !state.activeActivity) return null;
      let index = _.findIndex(
        state.activities,
        o => o.id === state.activeActivity.id
      );
      return state.activities[index + 1]
        ? state.activities[index + 1].id
        : null;
    },
    prevWorkflowActivity(state) {
      if (!state.activities || !state.activeActivity) return null;
      let index = _.findIndex(
        state.activities,
        o => o.id === state.activeActivity.id
      );
      return state.activities[index - 1]
        ? state.activities[index - 1].id
        : null;
    },
    workflowPeople(state) {
      if (!state.activities) return null;
    }
  }
};

export default workflow;



