<template>
  <div>
    <div class="activityContent--section" v-if="hyperlinks.length > 0">
      <h5>Links</h5>
      <div class="hyperlink" v-for="link in hyperlinks" v-bind:key="link.id">
        <a v-bind:href="link.address">{{ link.title }}</a>
        <button class="hyperlink--delete" @click="deleteHyperlink(link.id)">Delete</button>
        <p class="hyperlink--label">{{ link.metadata.userMetadata.modifiedBy.fullName }} - {{ moment(link.metadata.dateMetadata.modifiedOn).fromNow() }}</p>
      </div>
    </div>
    <div v-if="creating">  
      <input type="text" v-model="newHyperlinkLink" placeholder="URL"/>
      <input type="text" v-model="newHyperlinkText" placeholder="Description"/>
      <button @click="createHyperlink">Save</button>
      <button @click="cancelCreateHyperlink">Cancel</button>
    </div>
    <button v-else @click="startCreateHyperlink"><i class="icon-add-small"></i></button>
  </div>
</template>

<script>
export default {
  name: "Hyperlinks",
  props: ["hyperlinks", "apiURL"],
  data: function() {
    return {
      creating: false,
      newHyperlinkLink: "",
      newHyperlinkText: ""
    };
  },
  mounted() {
    this.$root.$on("startCreateHyperlink", this.startCreateHyperlink);
  },
  beforeDestroy() {
    this.$root.$off("startCreateHyperlink", this.startCreateHyperlink);
  },
  methods: {
    deleteHyperlink(id) {
      this.$emit("hyperlinkDeleted", id);
    },
    createHyperlink() {
      const hyperlink = {
        title: this.newHyperlinkText,
        address: this.newHyperlinkLink
      };

      this.$emit("hyperlinkCreated", hyperlink, callback => {
        if (callback) {
          this.newHyperlinkLink = "";
          this.newHyperlinkText = "";
          this.creating = false;
        } else console.log(callback);
      });
    },
    startCreateHyperlink() {
      this.creating = true;
    },
    cancelCreateHyperlink() {
      this.creating = false;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";
.hyperlink {
  &:hover {
    .hyperlink--delete {
      visibility: visible;
    }
  }
}

.hyperlink--delete {
  visibility: hidden;
}

.hyperlink--label {
  @include label-small;
}
</style>



