<template>
  <div class="workflow" v-if="activeWorkflow">
    
    <div class="workflow-details">        
      <WorkflowDetails/>
    </div>
    
    <div v-if="activeWorkflow && !workflowActivities">Loading Tasks...</div>
    
    <div class="activities">
      <div v-if="workflowActivities && workflowActivities.length > 0">
        <draggable v-model="workflowActivities" @end="dragEnd" :options="{delay: 100, ghostClass: 'sortable-ghost', chosenClass: 'sortable-chosen', dragClass: 'sortable-drag', forceFallback: true}">
        <ActivityTile 
            v-for="activity in workflowActivities" 
            v-bind:key="activity.id"
            v-bind:activity="activity"/>
        </draggable>
      </div>

      <ExpandingCreateControl
        v-bind:toggleButtonText="'Create Task'"
        v-bind:inputPlaceholder="'Task Title'"
        v-bind:createButtonText="'Add Task'"
        v-on:createTriggered="createActivity"
        />
      
    </div>

    <div class="workflow-data-popup" v-if="activeTab">
      <button @click="hideDataPopup" class="workflow-data-popup--close"><i class="icon-close"></i></button>
      <Comments
        v-if="activeTab === 'COMMENTS' && workflowComments"
        v-bind:comments="workflowComments" 
        v-on:commentCreated="createComment"/>
      <WorkflowUsers
        v-else-if="activeTab === 'PEOPLE' && workflowUsers"
        v-bind:displayMode="'DETAILED'"
        v-bind:users="workflowUsers" 
        v-bind:workflowID="activeWorkflow.id"/>
      <div v-else>
        Loading...
      </div>
    </div>

    <div class="workflow-footers">
      <WorkflowUsers
        v-bind:displayMode="'MINIMAL'"
        v-bind:users="workflowUsers" 
        v-bind:workflowID="activeWorkflow.id"/>

      <button @click="toggleWorkflowPeople">{{ workflowUsers ? workflowUsers.length : activeWorkflow.stats.process.people.count }} <i class="icon-user"></i></button>
      <button @click="toggleWorkflowComments">{{ workflowComments ? workflowComments.length : activeWorkflow.stats.comments.count }} <i class="icon-comments"></i></button>
            
    </div>

    <div class="activity-overlay" v-if="activeActivity">
      <ActivityDetails :workflowid="activeWorkflow.id"/>
    </div>
</div>
</template>

<script>
import store from "./../store/store.js";
import _ from "lodash";
import draggable from "vuedraggable";

import Comments from "./Comments.vue";
import ActivityTile from "./ActivityTile.vue";
import ActivityDetails from "./ActivityDetails.vue";
import WorkflowDetails from "./WorkflowDetails.vue";
import WorkflowUsers from "./WorkflowUsers.vue";

import ExpandingCreateControl from "./controls/ExpandingCreateControl.vue";

export default {
  name: "Workflow",
  props: ["apiURL"],
  components: {
    Comments,
    ActivityTile,
    ActivityDetails,
    WorkflowDetails,
    ExpandingCreateControl,
    WorkflowUsers,
    draggable
  },
  data() {
    return {
      activeTab: null
    };
  },
  computed: {
    activeWorkflow() {
      return store.state.workflow.metadata;
    },
    workflowUsers() {
      return store.state.workflow.people;
    },
    workflowComments() {
      return store.state.workflow.comments;
    },
    workflowActivities: {
      get() {
        return store.getters.activities;
      },
      set(value) {
        store.commit("updateWorkflowOrder", value);
      }
    },
    activeActivity() {
      return store.state.activeActivity;
    }
  },
  watch: {
    activeWorkflow() {
      this.loadWorkflowActivities();
    }
  },
  methods: {
    loadWorkflowActivities() {
      if (!this.activeWorkflow.id) return;
      store.dispatch("getWorkflowActivities");
    },

    createActivity(newActivityName) {
      if (newActivityName.length === 0) return;

      const positionY = _.last(this.workflowActivities)
        ? _.last(this.workflowActivities).diagramNode.position.positionY + 100
        : 5000;

      const newActivity = {
        type: "USER_TASK",
        positionX: 0,
        positionY: positionY,
        name: newActivityName
      };

      store.dispatch("addWorkflowActivity", newActivity);
    },

    dragEnd(e) {
      e.preventDefault();

      const dropIndex = e.newIndex;
      const prevActivityY =
        dropIndex > 0
          ? this.workflowActivities[dropIndex - 1].diagramNode.position
              .positionY
          : null;
      const nextActivityY =
        dropIndex < this.workflowActivities.length - 1
          ? this.workflowActivities[dropIndex + 1].diagramNode.position
              .positionY
          : null;
      let diff = 0;

      if (prevActivityY && nextActivityY)
        diff = prevActivityY + Math.floor((nextActivityY - prevActivityY) / 2);
      else if (prevActivityY) diff = prevActivityY + 100;
      else if (nextActivityY) diff = nextActivityY - 100;
      else return;

      const newPosition = {
        activityId: this.workflowActivities[dropIndex].id,
        positionX: 0,
        positionY: diff
      };

      store.dispatch("updateActivityPosition", newPosition);
    },

    loadWorkflowData(path) {
      store.dispatch("getWorkflowData", path);
    },

    hideDataPopup() {
      this.activeTab = null;
    },

    toggleWorkflowPeople() {
      console.log(this.workflowUsers);
      this.activeTab = this.activeTab === "PEOPLE" ? null : "PEOPLE";
    },

    toggleWorkflowComments() {
      this.activeTab = this.activeTab === "COMMENTS" ? null : "COMMENTS";
      if (this.activeTab === "COMMENTS") this.loadWorkflowData("comments");
    },

    createComment(comment, callback) {
      store
        .dispatch("addWorkflowData", {
          path: "comments",
          data: comment
        })
        .then(() => callback(true))
        .catch(err => callback(err));
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.workflow {
  grid-area: main;
  padding: spacing(1);
  display: grid;
  min-height: 0;
  grid-template-rows: min-content 1fr min-content;
  grid-template-areas:
    "workflowheader"
    "workflow"
    "workflowfooter";

  * {
    min-height: 0;
  }
}

.workflow-details {
  grid-area: workflowheader;
  padding-bottom: spacing(1);
  margin-bottom: spacing(1);
}

.workflow-footers {
  grid-area: workflowfooter;
  text-align: right;
  * {
    display: inline-block;
  }
}

.workflow-data-popup {
  position: fixed;
  bottom: spacing(5);
  right: spacing(2);
  padding: spacing(2);
  overflow-y: auto;
  width: 360px;
  max-height: calc(90% - 40px);
  background-color: white;
  box-shadow: $default-shadow;
}

.workflow-data-popup--close {
  position: absolute;
  top: 0;
  right: 0;
}

.activities {
  grid-area: workflow;
  overflow-y: auto;
  padding: spacing(1);
  padding-top: 0;

  * {
    min-height: 0;
  }
}

.activity-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: zindex(modal);
}
</style>




