import api from "./../api.js";

const workflows = {
  state: {
    workflows: null,
    workflowList: [],
    activeWorkflow: null
  },
  mutations: {
    setWorkflows(state, data) {
      let workflows = {};

      data.forEach(workflow => {
        workflows[workflow.id] = workflow;
        if (state.workflowList.indexOf(workflow.id) === -1)
          state.workflowList.push(workflow.id);
      });

      state.workflows = workflows;
    },
    addWorkflow(state, workflow) {
      state.workflows[workflow.id] = workflow;
      state.workflowList.push(workflow.id);
    }
  },
  actions: {
    getWorkflows(context) {
      api
        .getAll("workflows")
        .then(result => {
          context.commit("setWorkflows", result);
        })
        .catch(err => console.log(err));
    },
    addWorkflow(context, workflow) {
      api
        .postOne("workflows", workflow)
        .then(result => {
          context.commit("addWorkflow", result);
        })
        .catch(err => console.log(err));
    }
  },
  getters: {
    workflows(state) {
      if (!state.workflowList) return null;
      return state.workflowList.map(workflowId => state.workflows[workflowId]);
    }
  }
};

export default workflows;



