<template>
    <div class="activityDetails" @click="changeActivity(null)">
        <button class="prev-button" v-if="prevActivity" @click.stop="changeActivity(prevActivity)"><i class="icon-chevron-left"></i></button>
        <div class="activityModal" @click.stop="">
          <div class="activityContent" v-if="activeActivity">
            <Editable
              v-bind:content="activeActivity.name"
              v-bind:type="'h3'"
              v-bind:placeholder="'Activity Name'"
              v-bind:singleline="true"
              v-on:update="activeActivity.name = $event"
              v-on:doneEditing="updateActivityMeta"/>
            
            <Editable
              v-bind:content="activeActivity.description"
              v-bind:type="'p'"
              v-bind:placeholder="'Description'"
              v-bind:singleline="true"
              v-on:update="activeActivity.description = $event"
              v-on:doneEditing="updateActivityMeta"/>

            <h4>Status: {{ activeActivity.state }}</h4>
            <p>Created By: {{ activeActivity.metadata.userMetadata.modifiedBy.fullName }}</p>
            
            <ActivityUsers
              v-bind:users="assignments"
              v-bind:workflowID="workflowid"
              v-bind:activityID="activeActivity.id"/>

            <Checklists
              v-bind:checklists="activeActivity.checklists"
              v-bind:apiURL="`workflows/${workflowid}/activities/${activeActivity.id}/checklists`"/>
            
            <Hyperlinks 
              v-bind:hyperlinks="activeActivity.hyperlinks"
              v-on:hyperlinkDeleted="deleteHyperlink"
              v-on:hyperlinkCreated="createHyperlink"/>

            <Attachments 
              v-bind:attachments="activeActivity.attachments"
              v-bind:activityID="activeActivity.id"
              v-on:attachmentDeleted="deleteAttachment"
              v-on:uploadAttachment="uploadAttachment"/>
            
            <Comments
              v-bind:comments="activeActivity.comments"
              v-on:commentCreated="createComment"/>
            
            <div>
              <button @click="() => this.$root.$emit('createChecklist')">Add a Checklist</button>
              <button @click="() => this.$root.$emit('startCreateComment')">Comment</button>
              <button @click="() => this.$root.$emit('startCreateHyperlink')">Add a Link</button>
              <button class="button__negative" @click="deleteActivity">Delete</button>
            </div>
          </div>  
          <div v-else>
            <h3>Loading...</h3>
          </div>
        </div>
        <button class="next-button" v-if="nextActivity" @click.stop="changeActivity(nextActivity)"><i class="icon-chevron-right"></i></button>
    </div>
</template>

<script>
import store from "./../store/store.js";

import Comments from "./Comments.vue";
import Hyperlinks from "./Hyperlinks.vue";
import Attachments from "./Attachments.vue";
import Checklists from "./Checklists.vue";
import ActivityUsers from "./ActivityUsers.vue";

import Editable from "./controls/Editable.vue";

export default {
  name: "ActivityDetails",
  props: ["workflowid"],
  components: {
    Comments,
    Hyperlinks,
    Attachments,
    Checklists,
    ActivityUsers,
    Editable
  },

  computed: {
    activeActivity() {
      return store.state.activeActivity;
    },
    assignments() {
      return store.state.activeActivity.assignments;
    },
    nextActivity() {
      return store.getters.nextWorkflowActivity;
    },
    prevActivity() {
      return store.getters.prevWorkflowActivity;
    }
  },
  data: function() {
    return {
      activityDetails: null,
      creatingComment: false,
      creatingHyperlink: false
    };
  },
  methods: {
    changeActivity(id) {
      if (id === null) {
        store.commit("closeActivityDetails");
        return;
      }
      store.dispatch("setActiveActivity", id);
    },
    updateActivityMeta() {
      const update = {
        name: this.activeActivity.name,
        description: this.activeActivity.description,
        type: "USER_TASK"
      };

      store.dispatch("updateActivityMeta", update);
    },

    createComment(comment) {
      store.dispatch("addActivityData", {
        path: "comments",
        data: comment
      });
    },

    uploadAttachment(attachment) {
      store.commit("addActivityData", {
        data: attachment,
        path: "attachments"
      });
    },

    deleteAttachment(id) {
      store.dispatch("deleteActivityData", { path: "attachments", id: id });
    },

    deleteHyperlink(id) {
      store.dispatch("deleteActivityData", { path: "hyperlinks", id: id });
    },

    createHyperlink(hyperlink) {
      store.dispatch("addActivityData", {
        path: "hyperlinks",
        data: hyperlink
      });
    },

    deleteActivity() {
      store.dispatch("deleteActivity", this.activeActivity.id);
    }
  }
};
</script>

<style lang="scss">
@import "./../scss/vars";

.activityDetails {
  @include modal-background;
}
.activityModal {
  @include modal-window;
}

.activityContent--section {
  margin-top: spacing(2);
  padding-top: spacing(2);
  border-top: $default-border;
}

.prev-button {
  justify-self: end;
  grid-area: left;
}

.next-button {
  justify-self: start;
  grid-area: right;
}
</style>



