<template>
  <div>
    <div class="activityContent--section users" v-if="users.length > 0">
      <div class="users--assigned">
        <h5>Assigned User</h5>
        <div class="users--user" v-for="user in assignedActivityUser" v-bind:key="user.id">
          <User :user="user" :view-mode="'DETAILED'"/>
        </div>
      </div>
      <div class="users--subscribed">
        <h5>Subscribed Users</h5>
        <div class="users--user" v-for="user in subscribedActivityUsers" v-bind:key="user.id" @click="removeUser(user.id)">
          <User :user="user" :view-mode="'DETAILED'"/>
        </div>
      </div>
    </div>
    <dropdown :class-name="'dropdown'" :unscroll="'activityDetails'" :before-open="loadWorkflowUsers">
      <template slot="btn"><i class="icon-add-small"></i></template>
      <template slot="body">
        <div v-if="missingWorkflowUsers && missingWorkflowUsers.length > 0">
          <div class="users--dropdown" v-for="user in missingWorkflowUsers" v-bind:key="user.id">
            <div @click="assignUser(user)">
              <User :user="user" :view-mode="'DETAILED'"/>
            </div>
          </div>
        </div>
        <div v-else>
          No one left to assign :(
        </div>
      </template>
    </dropdown>
  </div>
</template>

<script>
import store from "./../store/store.js";
import _ from "lodash";
import Dropdown from "bp-vuejs-dropdown";

import User from "./User.vue";

export default {
  name: "Users",
  props: ["users", "activityID", "workflowID"],
  components: {
    User,
    Dropdown
  },
  computed: {
    workflowUsers() {
      return store.state.workflow.people;
    },
    assignedActivityUser() {
      return store.getters.assignedActivityUser;
    },
    subscribedActivityUsers() {
      return store.getters.subscribedActivityUser;
    },
    missingWorkflowUsers() {
      return _.differenceBy(
        this.workflowUsers,
        this.users.map(o => o.assignedTo),
        o => (o.user ? o.user.id : o.id)
      );
    }
  },
  methods: {
    loadWorkflowUsers(resolve) {
      if (this.workflowUsers) {
        resolve();
        return;
      }

      store.dispatch("getWorkflowData", "people").then(() => {
        resolve();
      });
    },
    assignUser(user) {
      store.dispatch("assignActivity", {
        activityID: this.activityID,
        userType: user.user ? "PERSON_USER" : "PERSON_PLACEHOLDER",
        userID: user.id
      });
    },
    removeUser(user) {
      console.log(user);
      //store.dispatch("removeAssignment",user);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.users {
  display: grid;
  grid-template-columns: min-content auto;
  grid-gap: spacing(2);
}

.users--assigned {
  white-space: nowrap;
}

.users--user {
  display: inline-block;
}
</style>



