<template>
  <div>
    <div class="activityContent--section" v-if="attachments.length > 0">
      <h5>Attachments</h5>
      <div v-for="attachment in attachments" v-bind:key="attachment.id">
        <div class="attachment"  v-if="getMimeType(attachment.mimeType) === 'IMAGE'">
          <div class="attachment--thumbnail">
            <img v-bind:src="`${baseURL}${attachment.downloadUrl}`"/>
          </div>
          <div class="attachment--description">
           <span class="attachment--title">{{ attachment.fileName }}</span>
           <p class="attachment--label">{{ attachment.metadata.userMetadata.modifiedBy.fullName }} - {{ moment(attachment.metadata.dateMetadata.modifiedOn).fromNow() }}</p>
          </div>
           <button class="attachment--delete button__negative" @click="deleteAttachment(attachment.id)">Delete</button>
        </div>
        <div class="attachment" v-else>
          <i class="icon-attachments-big attachment--thumbnail"></i>
          <div class="attachment--description">
            <a class="attachment--title" v-bind:href="`${baseURL}${attachment.downloadUrl}`" target="_BLANK">{{ attachment.fileName }}</a>
            <p class="attachment--label">{{ attachment.metadata.userMetadata.modifiedBy.fullName }} - {{ moment(attachment.metadata.dateMetadata.modifiedOn).fromNow() }}</p>
          </div>
          <button class="attachment--delete button__negative" @click="deleteAttachment(attachment.id)">Delete</button>
        </div>
        
        
      </div>
    </div>
    <button @click="()=>{this.creating = !this.creating}">Create</button>
    <div v-if="creating">  
      <form enctype="multipart/form-data" novalidate>
        <h3>Upload Content</h3>
        <div class="dropbox">
          <input type="file" name="attachments" multiple :disabled="isSaving" @change="getFiles" class="input-file"/>
          <p v-if="isSaving">Saving {{ fileCount }} files...</p>
          <p v-else>Drag your file(s) here to upload, or click to browse.</p>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import store from "./../store/store.js";
import { BASE_ATTACHMENT_URL } from "./../api.js";

export default {
  name: "Attachments",
  props: ["attachments", "activityID"],
  data: function() {
    return {
      creating: false,
      isSaving: false,
      fileCount: 0,
      baseURL: BASE_ATTACHMENT_URL
    };
  },
  mounted() {
    this.$root.$on("startCreateAttachment", this.startCreateAttachment);
  },
  beforeDestroy() {
    this.$root.$off("startCreateAttachment", this.startCreateAttachment);
  },
  computed: {
    activeWorkflowID() {
      return store.state.workflow.metadata.id;
    }
  },
  methods: {
    getFiles(e) {
      this.filesChange(e.target.files);
      this.fileCount = e.target.files.length;
    },

    filesChange(fileList) {
      const formData = new FormData();

      if (!fileList.length) return;

      this.isSaving = true;

      Array.from(Array(fileList.length).keys()).map(x => {
        formData.append("file", fileList[x]);
      });

      const activityURL = this.activityID
        ? "/activities/" + this.activityID
        : "";
      const postURL = `workflows/${
        this.activeWorkflowID
      }${activityURL}/attachments`;

      const upload = { postURL: postURL, formData: formData };

      this.$emit("uploadAttachment", upload, callback => {
        if (callback) {
          this.isSaving = false;
          this.creating = false;
        } else console.log(callback);
      });
    },

    deleteAttachment(id) {
      this.$emit("attachmentDeleted", id);
    },
    startCreateAttachment() {
      this.creating = true;
    },
    cancelCreateAttachment() {
      this.creating = false;
    },
    getMimeType(mimeType) {
      switch (mimeType) {
        case "image/gif":
        case "image/jpgeg":
        case "image/png":
        case "image/svg+xml":
          return "IMAGE";

        case "application/vnd.ms-excel":
        case "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet":
        case "text/csv":
          return "SPREADSHEET";

        case "application/vnd.openxmlformats-officedocument.presentationml.presentation":
        case "application/vnd.ms-powerpoint":
          return "PRESENTATION";

        case "application/msword":
        case "application/vnd.openxmlformats-officedocument.wordprocessingml.document":
          return "DOCUMENT";

        case "application/pdf":
          return "PDF";

        case "application/zip":
        case "application/x-rar-compressed":
          return "ARCHIVE";
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";
.attachment {
  display: grid;
  grid-template-areas: "thumb description actions";
  grid-template-columns: 60px 1fr min-content;
  grid-gap: spacing(2);

  &:hover {
    .attachment--delete {
      visibility: visible;
    }
  }
}

.attachment--thumbnail {
  width: 60px;
  height: 60px;
  overflow: hidden;
  grid-area: thumb;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.attachment--description {
  grid-area: description;
}

.attachment--delete {
  visibility: hidden;
  grid-area: actions;
}

.attachment--label {
  @include label-small;
}

.dropbox {
  outline: 2px dashed grey; /* the dash box */
  outline-offset: -10px;
  background: lightcyan;
  color: dimgray;
  padding: 10px 10px;
  min-height: 200px; /* minimum height */
  position: relative;
  cursor: pointer;
}

.input-file {
  opacity: 0; /* invisible but it's there! */
  width: 100%;
  height: 200px;
  position: absolute;
  cursor: pointer;
}

.dropbox:hover {
  background: lightblue; /* when mouse over to the drop zone, change color */
}
</style>



