<template>
  <div>
    <div class="activityContent--section users" v-if="realUsers && realUsers.length > 0" v-bind:class="[displayMode === 'MINIMAL'? 's-compact': '']">
      
      <div class="users--people">
        <h5 v-if="displayMode != 'MINIMAL'">Participating Users</h5>
        <div class="users--user" v-for="user in realUsers" v-bind:key="user.id">
          <User :user="user" :view-mode="displayMode"/>
        </div>
      </div>

      <div class="users--placeholder" v-if="placeholderUsers && placeholderUsers.length > 0">
        <h5 v-if="displayMode != 'MINIMAL'">Placeholders</h5>
        <div class="users--user" v-for="user in placeholderUsers" v-bind:key="user.id">
          <User :user="user" :view-mode="displayMode"/>
        </div>
      </div>
    </div>

    <dropdown :class-name="'dropdown'" ref="adduserdropdown" :before-open="loadContacts" v-if="displayMode != 'MINIMAL'">
        <template slot="btn"><i class="icon-add-small"></i></template>
        <template slot="body">
          <div v-if="missingContacts && missingContacts.length > 0">
            <div class="users--dropdown" v-for="user in missingContacts" v-bind:key="user.id">
              <h5>Contacts</h5>
              <div @click="inviteUser(user)">
                <User :user="user" :view-mode="'DETAILED'"/>
              </div>
            </div>
          </div>
          <div v-else>
             The gang's all here!! 
          </div>
          <hr/>
          <div>
            <ExpandingCreateControl
              v-bind:toggleButtonText="'Add Placeholder'"
              v-bind:inputPlaceholder="'Placeholder Name'"
              v-bind:createButtonText="'Add'"
              v-on:createTriggered="createPlaceholder"
              />
          </div>
        </template>
      </dropdown>
  </div>
</template>

<script>
import store from "./../store/store.js";
import _ from "lodash";
import Dropdown from "bp-vuejs-dropdown";

import User from "./User.vue";
import ExpandingCreateControl from "./controls/ExpandingCreateControl.vue";

export default {
  name: "WorkflowUsers",
  props: ["users", "workflowID", "displayMode"],
  components: {
    User,
    Dropdown,
    ExpandingCreateControl
  },
  data: function() {
    return {
      localUsers: this.users
    };
  },
  computed: {
    realUsers() {
      if (!store.state.workflow.people) return null;
      const ppl = store.state.workflow.people.filter(o => o.user);
      return this.displayMode === "MINIMAL" ? ppl.slice(0, 2) : ppl;
    },
    placeholderUsers() {
      if (!store.state.workflow.people) return null;
      const ppl = store.state.workflow.people.filter(o => !o.user);
      return this.displayMode === "MINIMAL" ? ppl.slice(0, 2) : ppl;
    },

    contacts() {
      return store.state.user.contacts;
    },
    missingContacts() {
      return _.differenceBy(this.contacts, this.realUsers, o => o.user.id);
    }
  },
  methods: {
    loadContacts(resolve) {
      if (this.contacts) {
        resolve();
        return;
      }

      store.dispatch("getUserContacts").then(() => {
        resolve();
      });
    },
    inviteUser(user) {
      const update = {
        accessLevel: "MEMBER",
        userId: user.userId
      };

      store.dispatch("inviteWorkflowContact", update);
    },
    createPlaceholder(name) {
      store.dispatch("createPlaceholder", name);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.users {
  display: block;

  &.s-compact {
    display: inline-block;

    .users--placeholder,
    .users--people {
      display: inline-block;
    }
  }
}

.users--user {
  display: inline-block;
}
</style>



