<template>
    <div class="workflow--details">
        <h2 class="workflow--title">
            <i class="icon-chevron-left" @click="toggleWorkflowList"></i>
            <span @click="toggleWorkflowDetails">{{ activeWorkflow.process.name }} - {{ activeWorkflow.process.state }} - {{ workflowActivities }} Tasks </span>
        </h2>
        
        <div class="workflow--actions">
            <button @click="toggleWorkflowHyperlinks">{{ workflowHyperlinks ? workflowHyperlinks.length : activeWorkflow.stats.process.hyperlinks.count }} <i class="icon-links"></i></button>
            <button @click="toggleWorkflowAttachments">{{ workflowAttachments ? workflowAttachments.length : activeWorkflow.stats.process.attachments.count }} <i class="icon-link"></i></button>
        </div>
        
        <div class="workflow--details-contents" v-if="activeTab === 'DETAILS'">
        <div>
            Created: {{ activeWorkflow.metadata.dateMetadata.createdOn }}
            <br/>Last Modified: {{ activeWorkflow.metadata.dateMetadata.modifiedOn }} by {{ activeWorkflow.metadata.userMetadata.modifiedBy.fullName }}
        </div>
        <div v-if="activeWorkflow.process.description">
            Description: {{ activeWorkflow.process.description }}
        </div>
        <div>
            {{ workflowActivities }} Tasks
            <ul>
            <li v-for="(value, key) in activeWorkflow.stats.process.activities.states" v-bind:key="key">{{ key }}:{{ value }}</li>
            </ul>
        </div>
        </div>

        <div class="workflow--details-contents" v-if="activeTab === 'HYPERLINKS'">
          <div v-if="!workflowHyperlinks">Loading Links...</div>
          <Hyperlinks 
              v-else
              v-bind:hyperlinks="workflowHyperlinks"
              v-on:hyperlinkDeleted="deleteHyperlink"
              v-on:hyperlinkCreated="createHyperlink"/>
        </div>

        <div class="workflow--details-contents" v-if="activeTab === 'ATTACHMENTS'">
          <div v-if="!workflowAttachments">Loading Attachments...</div>
          <Attachments 
              v-else
              v-bind:attachments="workflowAttachments"
              v-on:attachmentDeleted="deleteAttachment"
              v-on:uploadAttachment="uploadAttachment"/>
        </div>
    </div>
</template>

<script>
import store from "./../store/store.js";

import Hyperlinks from "./Hyperlinks.vue";
import Attachments from "./Attachments.vue";
import ExpandingCreateControl from "./controls/ExpandingCreateControl.vue";

export default {
  name: "WorkflowDetails",
  components: {
    Hyperlinks,
    Attachments,
    ExpandingCreateControl
  },
  data: function() {
    return {
      activeTab: null
    };
  },
  computed: {
    activeWorkflow() {
      return store.state.workflow.metadata;
    },
    workflowHyperlinks() {
      return store.state.workflow.hyperlinks;
    },
    workflowAttachments() {
      return store.state.workflow.attachments;
    },
    workflowUsers() {
      return store.state.workflow.people;
    },
    workflowActivities() {
      return store.state.workflow.activities
        ? store.state.workflow.activities.length
        : "0";
    }
  },
  methods: {
    toggleWorkflowList() {
      store.commit("toggleWorkflows");
    },

    toggleWorkflowDetails() {
      this.activeTab = this.activeTab === "DETAILS" ? null : "DETAILS";
    },
    toggleWorkflowHyperlinks() {
      this.activeTab = this.activeTab === "HYPERLINKS" ? null : "HYPERLINKS";
      if (this.activeTab === "HYPERLINKS") this.loadWorkflowData("hyperlinks");
    },
    toggleWorkflowAttachments() {
      this.activeTab = this.activeTab === "ATTACHMENTS" ? null : "ATTACHMENTS";
      if (this.activeTab === "ATTACHMENTS")
        this.loadWorkflowData("attachments");
    },

    loadWorkflowData(path) {
      store.dispatch("getWorkflowData", path);
    },

    uploadAttachment(attachment, callback) {
      store
        .dispatch("uploadAttachments", {
          path: attachment.postURL,
          data: attachment.formData
        })
        .then(() => callback(true))
        .catch(err => callback(err));
    },
    deleteAttachment(id) {
      store.dispatch("deleteWorkflowData", { path: "attachments", id: id });
    },

    createHyperlink(hyperlink, callback) {
      store
        .dispatch("addWorkflowData", {
          path: "hyperlinks",
          data: hyperlink
        })
        .then(() => callback(true))
        .catch(err => callback(err));
    },
    deleteHyperlink(id) {
      store.dispatch("deleteWorkflowData", { path: "hyperlinks", id: id });
    }
  }
};
</script>

<style lang="scss" scoped>
.workflow--details {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-areas:
    "details-title details-actions"
    "details-content details-content";
}

.workflow--details-contents {
  grid-area: details-content;
  max-width: 600px;
  margin: 0 auto;
}

.workflow--actions {
  grid-area: details-actions;
}

.workflow--title {
  grid-area: details-title;
}
</style>




