<template>
    <div class="activity-tile" :class="`s-${activity.state.toLowerCase()}`" @click="loadActivity(activity.id)" @dragover="dragOverTile" @drop="dropOverTile">
        <div class="activity-tille--assignee">
          <User :user="assignedUser"/>
        </div>
        <div class="activity-tile--text">
          <h3 class="activity-tile--title">{{ activity.name }}</h3>
          <span class="activity-tile--description" v-if="activity.description">{{ activity.description }}</span>
        </div>
    </div>
</template>

<script>
import store from "./../store/store.js";
import User from "./User.vue";

export default {
  name: "ActivityTile",
  props: ["activity"],
  components: {
    User
  },
  data: function() {
    return {};
  },
  computed: {
    assignedUser() {
      if (!this.activity.assignments) return null;

      const user = this.activity.assignments.filter(
        x => x.assignedAction === "EXECUTE"
      );
      return user[0] ? user[0].assignedTo : null;
    }
  },
  methods: {
    loadActivity(id) {
      store.dispatch("setActiveActivity", id);
    },
    dragOverTile(e) {
      e.preventDefault();
    },
    dropOverTile() {
      const update = {
        activityID: this.activity.id,
        userType: store.state.ui.draggedUser.type,
        userID: store.state.ui.draggedUser.id
      };
      console.log(update);
      store.dispatch("assignActivity", update);
      //e.preventDefault();
    }
  }
};
</script>

<style lang="scss">
@import "./../scss/vars";

.activity-tile {
  //@include containerBox;
  padding: spacing(1) spacing(2);
  display: grid;
  grid-template-columns: $avatar-size 1fr 1fr 1fr;
  grid-template-areas: "avatar header header header";
  grid-gap: spacing(2);
  text-align: left;

  &:hover {
    .activity-tile--action {
      visibility: visible;
    }
  }
}
.activity-tille--assignee {
  grid-area: avatar;
}

.activity-tile--text {
  grid-area: header;
}

.activity-tile--title {
  padding: 0;
  display: inline-block;
  margin-bottom: 0;
}

.activity-tile--description {
  display: inline-block;
  margin-left: spacing(2);
  font-style: italic;
}

.activity-tile--state {
  grid-area: state;
  font-weight: 700;
}

.activity-tile--meta {
  grid-area: meta;
}

.activity-tile--action {
  grid-area: header-right;
  visibility: hidden;
}
</style>



